import React from "react";
import { AppBar, Box, Toolbar } from "@mui/material";
import { useNavigate } from "react-router-dom"; // Importez useNavigate pour la redirection

function Header(props) {
  const navigate = useNavigate(); // Hook pour naviguer

  // Fonction pour grer le clic avec les touches Ctrl et Alt enfonces
  const handleLogoClick = (event) => {
    // Vrifie si les touches Ctrl et Alt taient enfonces lors du clic
    if (event.ctrlKey && event.altKey) {
      // Si oui, redirige vers /admin
      navigate("/admin");
    }
  };

  return (
    <AppBar
      sx={{
        justifyContent: "center",
        position: "sticky",
        top: "0",
        marginBottom: "2vh",
      }}
    >
      <Toolbar
        style={{
          justifyContent: "center",
          padding: "0",
        }}
      >
        <Box
          component="img"
          src={
            props.darkMode
              ? "/images/Logo_Stramatel_White.png"
              : "/images/Logo_Stramatel_Dark.png"
          }
          alt="Logo"
          sx={{ width: "200px", height: "auto" }}
          onClick={handleLogoClick} // Ajouter le gestionnaire d'vnements ici
        />
      </Toolbar>
    </AppBar>
  );
}

export default Header;
